button.square {
    width: 60px;
    height: 60px;
    font-size: 24px;
    margin: 2px;
    background-color: #f0f0f0;
    border: 1px solid #ccc;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}

button.square:hover {
    background-color: #e0e0e0;
}